body, html {
  font-family: 'Montserrat', Arial, sans-serif !important; }

.reveal-holder {
  position: relative;
  display: inline-block;
  overflow: hidden; }

.reveal-block {
  position: absolute;
  top: 0;
  width: 100%;
  height: 101%;
  background: #fff; }

.reveal-block.right::before {
  transform: scaleX(0);
  transform-origin: 0% 100%; }

.reveal-block.right.aos-animate::before {
  transform: scaleX(1); }

[data-aos="reveal-item"] {
  visibility: hidden;
  transition-property: visibility;
  transition-duration: 0s; }

[data-aos="reveal-item"].aos-animate {
  visibility: visible; }

.reveal-block::before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #be9a2f;
  transition-property: transform;
  transition-duration: .9s; }

[data-aos="reveal-right"] {
  transform: scaleX(1);
  transform-origin: 100% 0%;
  transition-property: transform;
  transition-delay: 1.2s; }

[data-aos="reveal-right"].aos-animate {
  transform: scaleX(0); }

.lds-dual-ring {
  display: inline-block;
  margin-top: 15px;
  width: 75px;
  height: 75px; }

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 48px;
  height: 48px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #be9a2f;
  border-color: #be9a2f transparent #be9a2f transparent;
  animation: lds-dual-ring 1.75s linear infinite; }

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
